import AbstractEditorModel from '../editorModels/AbstractEditorModel';

class CbCodeModel extends AbstractEditorModel {
  serviceName = '';
  constructor(serviceName: string) {
    super();
    this.serviceName = serviceName;
  }
  public getModels(): string {
    // When we are ready to implement this, this method should return
    // the models/interfaces for all "Libraries" used when editing code services
    // and code libraries, ie ClearBlade, log, http, etc.
    return `
      var ClearBlade: CbServer.ClearBladeInt;
      function log(logText: any): void;`;
  }
}

export default CbCodeModel;



