/**
 *
 * CodeSettingsForm
 *
 */

import React from 'react';
// import styled from 'styled-components';
import { Card } from 'material-ui-next';
import { FormattedMessage } from 'react-intl';
import CbInputField from 'components/CbInputField';
import messages from './messages';
import { SettingTypes } from 'utils/types';
import { ServiceModel } from 'cb-utils/console-entity-models';
import { Row, Button } from 'reactstrap';
import { RunAsUser } from 'containers/RunAsUser';
import {
  checkIsRunning,
  killService,
  ServiceCheck,
} from 'cb-utils/services/service';
import { RefreshIcon } from 'components/Icons';
import { friendlyTime } from 'utils/friendlyTime';

interface CodeSettingsFormProps {
  service: ServiceModel;
  onChange: (arg: Partial<ServiceModel>) => void;
}

class CodeSettingsForm extends React.PureComponent<CodeSettingsFormProps> {
  state: { servicesRunning?: ServiceCheck[] } = {
    servicesRunning: [],
  };
  handleCheckRunning = () => {
    checkIsRunning(this.props.service.name).then((res) => {
      this.setState({ servicesRunning: res });
    });
  };
  componentDidMount() {
    this.handleCheckRunning();
  }
  handleKillService = (id: string) => {
    killService(id).then(() => this.handleCheckRunning());
  };
  render() {
    return (
      <div>
        <RunAsUser
          onChange={(e) => {
            this.props.onChange({
              run_user: e.target.value,
            });
          }}
          value={this.props.service.run_user}
        />
        <Row>
          <hr />
          <CbInputField
            labelText={<FormattedMessage {...messages.concurrency} />}
            type={SettingTypes.NUMBER_TYPE}
            onChange={(e) =>
              this.props.onChange({ concurrency: Number(e.target.value) })
            }
            value={this.props.service.concurrency}
            name="concurrency"
            display_name="concurrency"
          />
          <CbInputField
            labelText={<FormattedMessage {...messages.execution_timeout} />}
            type={SettingTypes.NUMBER_TYPE}
            onChange={(e) =>
              this.props.onChange({ execution_timeout: Number(e.target.value) })
            }
            value={this.props.service.execution_timeout}
            name="execution_timeout"
            display_name="execution_timeout"
          />
          <CbInputField
            labelText={<FormattedMessage {...messages.auto_restart} />}
            type={SettingTypes.BOOLEAN_TYPE}
            onChange={(e) =>
              this.props.onChange({ auto_restart: e.target.value })
            }
            value={this.props.service.auto_restart}
            name="auto_restart"
            display_name="auto_restart"
          />
        </Row>
        <Button onClick={this.handleCheckRunning}>
          <RefreshIcon />
        </Button>{' '}
        <FormattedMessage {...messages.servicesRunning} />
        {this.state.servicesRunning.length
          ? this.state.servicesRunning.map((serviceRunning) => (
              <Card key={serviceRunning.Id} className="my-3 mx-1 p-3">
                <Button
                  className="float-right"
                  data-cy="kill-running-service"
                  onClick={() => this.handleKillService(serviceRunning.Id)}
                >
                  <FormattedMessage {...messages.killCodeService} />
                </Button>
                <FormattedMessage {...messages.started} />
                <b>{friendlyTime(serviceRunning.Started * 1000)}</b>
                <br />
                <FormattedMessage {...messages.executingAs} />
                {serviceRunning.ExecutingAs}
              </Card>
            ))
          : 'None'}
      </div>
    );
  }
}

export default CodeSettingsForm;



