export interface IValues {
  name: string;
  description: string;
  start_time: string;
  repeats: number;
  frequencyNumber: number;
  frequencyType: FrequencyType;
  repeatsName: RepeatsOptions;
  service_name: string;

  // these values are not used but from saved timer for simpler conversion back and forth
  timer_key?: string;
  system_key?: string;
  system_secret?: string;
  user_id?: string;
  user_token?: string;
}

export enum RepeatsOptions {
  forever = 'forever',
  once = 'once',
  other = 'other',
}

export enum FrequencyType {
  Seconds = 'Seconds',
  Minutes = 'Minutes',
  Hours = 'Hours',
  Days = 'Days',
  Weeks = 'Weeks',
}

export const secondsIn = {
  Seconds: 1,
  Minutes: 60,
  Hours: 3600,
  Days: 86400,
  Weeks: 604800,
};



