export default function makeOptions(options: {} | any[]) {
  if (Array.isArray(options)) {
    return options.map((v) => ({
      value: v,
      name: v,
    }));
  }
  return Object.keys(options).map((v) => ({
    value: v,
    name: v,
  }));
}



