import { takeEvery, put, all } from 'redux-saga/effects';
import { createShowBaseModal } from '../Console/actions/ui';
import { createSetServiceResult, getServiceLogs, runService } from './actions';
import * as ducks from 'containers/Console/ducks';
import { cb } from 'cb-utils/cbLib';

const triggerRunService = function*() {
  yield takeEvery(ducks.services.actions.update.successType, function*(
    action: ReturnType<typeof ducks.services.actions.update.success>,
  ) {
    if (action.meta.runCode) {
      yield put(
        runService.actions.request({
          name: action.payload.name,
          paramObj: action.payload.paramObj,
        }),
      );
    }
  });
};

const runServiceComplete = function*() {
  yield takeEvery(runService.actions.successType, function*(
    action: ReturnType<typeof runService.actions.success>,
  ) {
    const results = action.payload;
    const serviceName = results.name;
    yield put(
      createShowBaseModal({
        component: 'CodeResponseModal',
        size: 'lg',
        props: results,
      }),
    );
    yield put(createSetServiceResult(results));
    yield put(getServiceLogs.actions.request({ serviceName }));
    if (!results.success) {
      yield put(
        ducks.failedRuns.actions.fetchCount.request({
          query: cb
            .Query()
            .setPage(1, 0)
            .equalTo('service', serviceName),
        }),
      );
    }
  });
};

// All sagas to be loaded
export default function*() {
  yield all([
    triggerRunService(),
    runServiceComplete(),
    getServiceLogs.saga(),
    runService.saga(),
  ]);
}



