import * as React from 'react';
import Toggle from 'react-toggle';
import styled from 'styled-components';
import 'react-toggle/style.css';

import withStyledProps from 'cb-utils/withStyledProps';

interface ToggleProps {
  value: boolean;
  onChange: (name?: string, value?: boolean) => void;
  name?: string;
  color?: string;
}

interface ToggleWrapperProps {
  color: string;
  onChange: (name: string, value: string) => void;
  checked: boolean;
  icons: boolean;
  className: string;
}

const ToggleWrapper = withStyledProps<ToggleWrapperProps>()(styled(Toggle))`
  .react-toggle-track {
    background-color: ${(props) => props.color} !important;
  }
  .react-toggle-thumb {
    border-color: ${(props) => props.color} !important;
  }
`;

class ToggleWidget extends React.PureComponent<ToggleProps> {
  _toggleSwitch = (name: string) => {
    this.props.onChange(name, !this.props.value);
  };

  render() {
    const { value, name, color } = this.props;
    return (
      <ToggleWrapper
        color={color}
        onChange={() => this._toggleSwitch(name)}
        checked={value}
        icons={false}
        className="toggle-switch"
      />
    );
  }
}

export default ToggleWidget;



