/**
 *
 * NewTriggers
 *
 */

import React, { SFC } from 'react';
// import styled from 'styled-components';
import * as ducks from 'containers/Console/ducks';

import messages from './messages';
import { Formik, FormikProps, Field } from 'formik';
import { assetNameValidations } from 'utils/validations';
import FormikForm from 'components/FormikForm';
import CbButton from 'components/CbButton';
import { Button } from 'reactstrap';
import AssetClassBasedFields, { IValues } from './AssetClassBasedFields';
import FormikInputWrapper from 'components/FormikInputWrapper';
import { injectIntl, FormattedMessage } from 'react-intl';
import Yup from 'yup';
import { SettingTypes, CBEvent } from 'utils/types';
import {
  defNameFromModuleOptions,
  DefModuleOptions,
  TriggerModel,
} from 'cb-utils/console-entity-models';
import makeOptions from 'utils/makeOptions';
import { connect } from 'react-redux';
import { getSystemKey } from 'cb-utils/getPathParams';

interface NewTriggersProps {
  serviceName?: string;
  savedTrigger?: TriggerModel;
  onCancel?: () => void;
}

function modelToValues({
  event_definition,
  ...trigger
}: TriggerModel): IValues {
  return {
    ...event_definition,
    ...trigger,
  };
}

const NewTriggers: SFC<
  NewTriggersProps & IDispatchToProps & ReactIntl.InjectedIntlProps
> = (props) => {
  const { formatMessage } = props.intl;

  const sharedProps = {
    disabled: !!props.savedTrigger,
    component: FormikInputWrapper,
  };

  return (
    <div>
      <Formik
        validationSchema={Yup.object().shape({
          name: assetNameValidations(props.intl),
          def_module: Yup.string().required(formatMessage(messages.required)),
          def_name: Yup.string().required(formatMessage(messages.required)),
        })}
        initialValues={
          props.savedTrigger ? modelToValues(props.savedTrigger) : {}
        }
        onSubmit={(
          { def_module, def_name, key_value_pairs, name }: IValues,
          actions,
        ) => {
          const newTrigger = {
            system_key: getSystemKey(),
            def_module,
            def_name,
            name,
            key_value_pairs: key_value_pairs || {},
            service_name: props.serviceName,
          };
          if (props.savedTrigger) {
            props.updateTrigger(newTrigger);
          } else {
            props.createTrigger(newTrigger);
            props.onCancel();
          }
        }}
        render={(fProps: FormikProps<IValues>) => {
          const actionOptions =
            defNameFromModuleOptions[fProps.values.def_module] &&
            defNameFromModuleOptions[fProps.values.def_module].map((opt) => ({
              value: opt,
              // @ts-ignore
              name: formatMessage(messages[opt]),
            }));

          return (
            <FormikForm {...fProps}>
              <Field
                {...sharedProps}
                name="name"
                label={<FormattedMessage {...messages.name} />}
              />
              <Field
                {...sharedProps}
                name="def_module"
                isFakeEvent
                useButtons
                type={SettingTypes.OPTION_TYPE}
                options={makeOptions(DefModuleOptions)}
                label={<FormattedMessage {...messages.def_module} />}
                onChange={({ target }: CBEvent) => {
                  const def_module = target.value as DefModuleOptions;

                  fProps.setValues({
                    name: fProps.values.name,
                    def_module,
                  });
                  // clear out other values
                  // bugs with { def_module: User, def_name: DeviceUpdate } if not cleared
                }}
              />
              {fProps.values.def_module && (
                <Field
                  {...sharedProps}
                  name="def_name"
                  isFakeEvent
                  type={SettingTypes.OPTION_TYPE}
                  options={[{ value: '', name: formatMessage(messages.select) }, ...actionOptions]}
                  label={<FormattedMessage {...messages.def_name} />}
                />
              )}
              {fProps.values.def_name && (
                <AssetClassBasedFields
                  {...fProps}
                  savedTrigger={props.savedTrigger}
                />
              )}
              {!props.savedTrigger && (
                <Button className="mr-2" onClick={props.onCancel}>
                  <FormattedMessage {...messages.cancel} />
                </Button>
              )}
              <CbButton
                color="primary"
                touched={fProps.touched}
                errors={fProps.errors}
              >
                {props.savedTrigger ? (
                  <FormattedMessage {...messages.update} />
                ) : (
                  <FormattedMessage {...messages.create} />
                )}
              </CbButton>
            </FormikForm>
          );
        }}
      />
    </div>
  );
};

const mapDispatchToProps = {
  createTrigger: ducks.triggers.actions.create.request,
  updateTrigger: ducks.triggers.actions.update.request,
};

type IDispatchToProps = typeof mapDispatchToProps;

export default connect(null, mapDispatchToProps)(injectIntl(NewTriggers));



