/*
 *
 * ServiceDetailPage actions
 *
 */

import {
  ActionConstants,
  GET_SERVICE_LOGS,
  RUN_SERVICE,
} from './actionConstants';
import {
  ServiceResult,
  getLogs,
  runServiceCall,
} from 'cb-utils/services/service';
import { getActionsSagaReducer } from 'utils/getActionsSagaReducer';

export const createSetServiceResult = (result?: ServiceResult) => ({
  type: ActionConstants.SET_SERVICE_RESULT,
  payload: result,
});
export type SetServiceResult = ReturnType<typeof createSetServiceResult>;

// GET_SERVICE_LOGS
export const getServiceLogs = getActionsSagaReducer({
  actionName: GET_SERVICE_LOGS,
  promise: getLogs,
});
export const createGetServiceLogs = getServiceLogs.actions.request;
type GetLogsResult = ReturnType<typeof getServiceLogs.actions.success>;

// RUN_SERVICE
export const runService = getActionsSagaReducer({
  actionName: RUN_SERVICE,
  promise: runServiceCall,
});
export const createRunService = runService.actions.request;
type RunServiceResult = ReturnType<typeof runService.actions.success>;

export type ActionTypes = SetServiceResult | GetLogsResult | RunServiceResult;



