/*
 *
 * SuccinctTrigger
 *
 */

import React from 'react';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import {
  TriggerModel,
  DefModuleOptions,
  AnyMap,
} from 'cb-utils/console-entity-models';
import {
  DatabaseIcon,
  DevicesIcon,
  MessagesIcon,
  UserIcon,
  TriggerIcon,
} from 'components/EntityIcons';
import { detailUrls } from 'cb-utils/getUrls';
import { Link } from 'react-router-dom';

const DefModuleToIcon: { [k: string]: JSX.Element } = {
  [DefModuleOptions.Data]: <DatabaseIcon />,
  [DefModuleOptions.Device]: <DevicesIcon />,
  [DefModuleOptions.Messaging]: <MessagesIcon />,
  [DefModuleOptions.User]: <UserIcon />,
};

const getLink = (key_value_pairs: AnyMap) => {
  const key = Object.keys(key_value_pairs)[0];
  const value = key_value_pairs[key];
  if (value === '*') return '';
  switch (key) {
    case 'collectionName':
      return <Link to={detailUrls.collections(value)}>{value}</Link>;
    case 'topic':
      return <Link to={detailUrls.messages(value)}>{value}</Link>;
    default:
      return value && <b>{value}</b>;
  }
};

interface SuccinctTriggerProps extends TriggerModel {
  showLinks?: boolean;
}

export const SuccinctTrigger = ({
  name,
  event_definition,
  key_value_pairs = {},
  showLinks = false,
}: SuccinctTriggerProps) => {
  const { collectionId, ...otherValuePairs } = key_value_pairs;
  return (
    <div>
      <TriggerIcon /> <FormattedMessage {...messages.on} />{' '}
      {DefModuleToIcon[event_definition.def_module]}{' '}
      {showLinks ? getLink(otherValuePairs) : Object.values(otherValuePairs)[0]}{' '}
      {<FormattedMessage {...messages[event_definition.def_name]} />}
    </div>
  );
};

export default SuccinctTrigger;



