import React from 'react';
import { connect } from 'react-redux';
import { edgeActions, edgeSelectors } from 'containers/Console/ducks';
import {
  createTestEdgeToken,
  createHideEdgeLoginError,
} from 'containers/Console/actions/currentUser';
import {
  AddIcon,
  CheckIcon,
  ExIcon,
  CaretDownIcon,
  SettingsIcon,
  EdgesIcon,
} from 'components/EntityIcons';
import { Button } from 'reactstrap';
import {
  EdgeModel,
  EdgeDevTokenInfo,
  PrimitiveType,
} from 'cb-utils/console-entity-models';
import {
  createShowBaseModal,
  createShowConfirmationModal,
} from 'containers/Console/actions/ui';
import PageHeader from 'components/ConsolePageHeader';
import ConsolePage from 'components/ConsolePage';
import Helmet from 'react-helmet';
import { Column } from 'react-table';
import CbDropDown from 'components/CbDropDown';
import ConsoleCrudgridContainer from 'containers/ConsoleCrudgridContainer';
import { format } from 'date-fns';
import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';
import { CBDispatch } from 'cb-utils/typeUtils';
import { RouteComponentProps } from 'react-router';
import styleVars from 'cssOverrides/styleVars';
import { getLoggedInEdges } from 'cb-utils/devToken';
import { selectProxyError } from 'containers/Console/selectors/currentUser';
import ErrorModal from 'components/ErrorModal';
import { bindActionCreators } from 'redux';
import { createStructuredSelector } from 'reselect';

export const GearDropDown = (props: { onClick?: (e?: any) => void }) => (
  <span className="float-right" {...props}>
    <SettingsIcon
      color={styleVars.gray600}
      data-cy="gear"
      style={{ width: '1rem' }}
    />
    <CaretDownIcon
      color={styleVars.gray600}
      style={{ width: '1rem', marginLeft: '-.1em' }}
    />
  </span>
);

interface EdgeNameProps {
  edgeDevToken: EdgeDevTokenInfo;
  edge: EdgeModel;
  createShowBaseModal: typeof createShowBaseModal;
  createTestEdgeToken: (edgeName: string) => void;
}

const EdgeName = (props: EdgeNameProps) => {
  if (!props.edge.isConnected) {
    return <span className="name">{props.edge.name}</span>;
  }
  return props.edgeDevToken ? (
    <a
      href="#"
      onClick={() => {
        props.createTestEdgeToken(props.edge.name);
      }}
      className="name"
    >
      {props.edge.name}
    </a>
  ) : (
    <span>
      <a
        href="#"
        onClick={() =>
          props.createShowBaseModal({
            component: 'LoginForm',
            props: { edgeId: props.edge.name },
          })
        }
        className="name"
      >
        {props.edge.name}{' '}
        <span className="text-muted">
          <FormattedMessage {...messages.notLoggedIn} />
        </span>
      </a>
    </span>
  );
};

const transformColumns = (cols: Column[], props: MasterProps) => {
  // isn't properly immutable change
  const newCols = cols.map((col) => {
    const colName = col.accessor;
    switch (colName) {
      case 'last_talked':
        col.Header = 'setup date';
        col.Cell = (cellInfo) => {
          const val = cellInfo.row[colName];
          return !!val ? (
            format(new Date(val * 1000), `MMM DD YYYY, hh:mm a `)
          ) : (
            <a
              href="#"
              data-cy="edge-setup"
              id={cellInfo.row.name}
              onClick={() =>
                props.createShowBaseModal({
                  component: 'EdgeSetup',
                  props: { id: cellInfo.row.name },
                })
              }
            >
              Setup instructions
            </a>
          );
        };
        return col;
      case 'isConnected':
        col.Header = 'connected';
        col.Cell = (cellInfo) => {
          const isConnected = cellInfo.row[colName];
          return isConnected ? <CheckIcon /> : <ExIcon color="darkred" />;
        };
        return col;
      case 'name':
        col.Cell = (cellInfo) => {
          const loggedInEdges = getLoggedInEdges();
          const edgeName = cellInfo.row[colName];
          const edgeDevToken = loggedInEdges[edgeName];
          const edge = props.edges[edgeName];
          return (
            <span>
              <EdgeName
                edge={edge}
                edgeDevToken={edgeDevToken}
                createTestEdgeToken={props.createTestEdgeToken}
                createShowBaseModal={props.createShowBaseModal}
              />{' '}
              <CbDropDown
                anchorEl={<CaretDownIcon /> as any}
                triggerEl={<GearDropDown />}
                items={[
                  {
                    displayName: (
                      <FormattedMessage {...messages.editViewDetails} />
                    ),
                    onClick: () =>
                      props.createShowBaseModal({
                        component: 'NewEdgeModal',
                        props: {
                          assetClass: 'Edges',
                          edge: props.edges[edgeName],
                          isEdit: true,
                        },
                      }),
                  },
                  {
                    displayName: (
                      <FormattedMessage {...messages.setUpInstructions} />
                    ),
                    onClick: () =>
                      props.createShowBaseModal({
                        component: 'EdgeSetup',
                        props: { id: edgeName },
                      }),
                  },
                ]}
              />
            </span>
          );
        };
        return col;
      default:
        return col;
    }
  });
  return newCols;
};

interface EdgesPageProps extends RouteComponentProps<{ systemKey: string }> {
  totalCount: number;
  rows: string[];
}

type MyProps = EdgesPageProps & ReactIntl.InjectedIntlProps;
type MasterProps = MyProps & StateToProps & DispatchToProps;

class EdgesPage extends React.PureComponent<MasterProps, { query: any }> {
  render() {
    const title = (
      <PageHeader
        header={
          <div>
            <EdgesIcon /> <FormattedMessage {...messages.header} />
          </div>
        }
        actionButton={() => (
          <Button
            onClick={() =>
              this.props.createShowBaseModal({ component: 'NewEdgeModal' })
            }
            data-cy="add-edge"
            color="primary"
          >
            <AddIcon />
            &nbsp; <FormattedMessage {...messages.addButton} />
          </Button>
        )}
      />
    );
    const systemKey = this.props.match.params.systemKey;

    return (
      <ConsolePage subheader={title}>
        <Helmet
          title="ClearBlade - Edges"
          meta={[{ name: 'description', content: '' }]}
        />
        {this.props.proxyError && (
          <ErrorModal
            title="Edge Login Error"
            onClose={this.props.createHideEdgeLoginError}
            body={this.props.proxyError}
          />
        )}
        <ConsoleCrudgridContainer
          assetClass="edges"
          systemKey={systemKey}
          deleteEventCallback={this.props.deleteEdge}
          deleteIsLoading={this.props.deleteIsLoading}
          createIsLoading={this.props.createIsLoading}
          allowSelect
          exportCallback={() =>
            this.props.createShowBaseModal({
              component: 'ExportModal',
              props: { assetClass: 'edges' },
            })
          }
          updateEventCallback={(
            edge: EdgeModel,
            colName: string,
            val: PrimitiveType,
          ) => {
            this.props.updateEdge({
              name: edge.name,
              system_key: systemKey,
              [colName]: val,
            });
          }}
          showEditColumns
          columnNames={[
            'name',
            'isConnected',
            'last_talked',
            'description',
          ].concat(Object.keys(this.props.customColumns))}
          transformColumns={(cols: Column[]) => {
            return transformColumns(cols, this.props);
          }}
        />
      </ConsolePage>
    );
  }
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  edges: edgeSelectors.entities,
  isLoading: edgeSelectors.fetchAll.isLoading,
  deleteIsLoading: edgeSelectors.deleteMultiple.isLoading,
  createIsLoading: edgeSelectors.create.isLoading,
  customColumns: edgeSelectors.customColumns(),
  proxyError: selectProxyError(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

const mapDispatchToProps = (dispatch: CBDispatch, ownProps: MyProps) => ({
  ...bindActionCreators(
    {
      fetchAllQuery: edgeActions.fetchAllQuery.request,
      createEdge: edgeActions.create.request,
      updateEdge: edgeActions.update.request,
      fetchColumns: edgeActions.fetchColumns.request,
      fetchCount: edgeActions.fetchCount.request,
      createShowBaseModal,
      createTestEdgeToken,
      createHideEdgeLoginError,
    },
    dispatch,
  ),
  deleteEdge: (edges: string[]) =>
    dispatch(
      createShowConfirmationModal(
        `${ownProps.intl.formatMessage(messages.deleteConfirmation, {
          number: 3,
        })} ${
          edges.length > 1 ? `${edges.length} edges` : `edge '${edges[0]}'`
        }?`,
        edgeActions.deleteMultiple.request(edges),
      ),
    ),
});

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(EdgesPage),
);



