import React from 'react';
// import styled from 'styled-components';
import { formatCamelCaseToSpace } from 'cb-utils/formatCamelCaseToSpace';
import messages from './messages';
import { FormikProps, Field } from 'formik';
import FormikInputWrapper from 'components/FormikInputWrapper';
import { FormattedMessage } from 'react-intl';
import { SettingTypes, CBEvent } from 'utils/types';
import {
  DefModuleOptions,
  DataOptions,
  UserOptions,
  DeviceOptions,
  MessagingOptions,
  AnyMap,
  DefNameOptions,
  TriggerModel,
} from 'cb-utils/console-entity-models';
import makeOptions from 'utils/makeOptions';
import { connect } from 'react-redux';
import * as ducks from 'containers/Console/ducks';
import { fetchUsers } from 'cb-utils/services/users';
import { fetchDevices } from 'cb-utils/services/devices';
import { createStructuredSelector } from 'reselect';

function capitalize(string: string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

export interface IValues {
  name: string;
  def_module?: DefModuleOptions;
  def_name?: DefNameOptions;
  key_value_pairs?: AnyMap;

  // these values are not used but from saved trigger for simpler conversion back and forth
  system_key?: string;
  system_secret?: string;
  service_name?: string;
  def_keys?: string[];
  visibility?: boolean;
}

const NO_SECOND_ACTIONS: DefNameOptions[] = [
  DataOptions.CollectionCreated,
  UserOptions.UserCreated,
  DeviceOptions.DeviceCreated,
];

function getKeyName(defModule: DefModuleOptions, defName: DefNameOptions) {
  switch (defModule) {
    case DefModuleOptions.Data:
      return 'collectionName';
    case DefModuleOptions.User:
      return 'userId';
    case DefModuleOptions.Device:
      return 'deviceName';
    case DefModuleOptions.Messaging:
      if (
        defName === MessagingOptions.MQTTDeviceConnected ||
        defName === MessagingOptions.MQTTDeviceDisconnected
      ) {
        return 'deviceName';
      }
      if (
        defName === MessagingOptions.MQTTUserConnected ||
        defName === MessagingOptions.MQTTUserDisconnected
      ) {
        return 'email';
      }
      return 'topic';
    default:
      return '';
  }
}

const ALL_OPTION = [{ name: 'All', value: '*' }];

interface IProps extends FormikProps<IValues> {
  savedTrigger: TriggerModel;
}

class AssetClassBasedFields extends React.PureComponent<
  IProps & IStateToProps & IDispatchToProps
> {
  componentWillMount() {
    if (
      !this.props.hasFetchedCollections &&
      !this.props.isFetchingCollections
    ) {
      // don't go crazy with requests if you have multiple of these forms on page
      this.props.fetchCollections();
    }
  }
  render() {
    const { setFieldValue, values, collections } = this.props;
    if (NO_SECOND_ACTIONS.includes(values.def_name)) return null;
    // create device trigger does not make sense with a specific device, though update and delete do
    // so there are no more options needed for create

    const keyName = getKeyName(values.def_module, values.def_name);
    const value = values.key_value_pairs && values.key_value_pairs[keyName];
    const onChange = ({ target }: CBEvent) => {
      setFieldValue('key_value_pairs', { [keyName]: target.value });
    };

    const sharedProps = {
      value,
      onChange,
      component: FormikInputWrapper,
      name: 'key_value_pairs',
    };

    switch (values.def_module) {
      case DefModuleOptions.Data:
        return (
          <Field
            useSelect
            type={SettingTypes.OPTION_TYPE}
            options={ALL_OPTION.concat(makeOptions(collections))}
            label={<FormattedMessage {...messages.collection} />}
            {...sharedProps}
          />
        );
      case DefModuleOptions.User:
        return (
          <Field
            type={SettingTypes.ASYNC_TYPE_AHEAD_TYPE}
            labelKey="email"
            idKey="user_id"
            fetchPromise={fetchUsers}
            label={<FormattedMessage {...messages.user} />}
            {...sharedProps}
          />
        );
      case DefModuleOptions.Device:
        return (
          <Field
            isFakeEvent
            type={SettingTypes.ASYNC_TYPE_AHEAD_TYPE}
            labelKey="name"
            idKey="name"
            fetchPromise={fetchDevices}
            label={<FormattedMessage {...messages.device} />}
            {...sharedProps}
          />
        );
      case DefModuleOptions.Messaging:
        const label = capitalize(formatCamelCaseToSpace(keyName));
        if (keyName === 'topic') {
          return <Field label={label} {...sharedProps} />;
        }
        return (
          <Field
            isFakeEvent
            type={SettingTypes.ASYNC_TYPE_AHEAD_TYPE}
            labelKey={keyName === 'deviceName' ? 'name' : keyName}
            idKey={keyName === 'deviceName' ? 'name' : keyName}
            fetchPromise={keyName === 'deviceName' ? fetchDevices : fetchUsers}
            label={label}
            {...sharedProps}
          />
        );
      default:
        return null;
    }
  }
}

const mapStateToProps = createStructuredSelector({
  collections: ducks.collections.selectors.entities,
  hasFetchedCollections: ducks.collections.selectors.hasFetched,
  isFetchingCollections: ducks.collections.selectors.fetchAll.isLoading,
  users: ducks.users.selectors.entities,
  devices: ducks.devices.selectors.entities,
});

type IStateToProps = ReturnType<typeof mapStateToProps>;

const mapDispatchToProps = {
  fetchCollections: ducks.collections.actions.fetchAll.request,
};
type IDispatchToProps = typeof mapDispatchToProps;

export default connect(mapStateToProps, mapDispatchToProps)(
  AssetClassBasedFields,
);



