/*
 *
 * EditTriggers
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import NewTrigger from 'containers/NewTrigger';
import { Button } from 'reactstrap';
import * as ducks from 'containers/Console/ducks';
import { Card } from 'material-ui-next';
import { TriggerModel } from 'cb-utils/console-entity-models';
import { TrashIcon, AddIcon } from 'components/EntityIcons';
import { SuccinctTrigger } from 'containers/NewTrigger/SuccinctTrigger';
import { createShowConfirmationModal } from '../Console/actions/ui';
import consoleMessages from 'containers/Console/messages';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

interface ITriggerSectionProps extends TriggerModel {
  deleteTrigger: (name: string) => any;
  // would have liked to use `typeof ducks.triggerActions.delete.request`
  // but wrapping with confirm causes mismatch type conflict
}

const TriggerSection = ({
  deleteTrigger,
  ...trigger
}: ITriggerSectionProps) => {
  return (
    <Card className="my-3 mx-1 p-3" id={trigger.name}>
      <Button
        className="float-right"
        data-cy="delete-trigger"
        onClick={() => deleteTrigger(trigger.name)}
      >
        <TrashIcon />
        <FormattedMessage {...messages.deleteTrigger} />
      </Button>
      <FormattedMessage {...consoleMessages.summary} />
      <b>
        <SuccinctTrigger {...trigger} showLinks />
      </b>
      <NewTrigger savedTrigger={trigger} />
    </Card>
  );
};

interface EditTriggersProps {
  serviceName: string;
}

export class EditTriggers extends React.PureComponent<
  EditTriggersProps & IStateToProps & IDispatchToProps,
  {
    showNew: boolean;
  }
> {
  state = { showNew: false };
  componentWillMount() {
    this.props.fetchTriggers();
  }
  render() {
    return (
      <div>
        {this.state.showNew ? (
          <Card className="my-3 mx-1 p-3">
            <NewTrigger
              serviceName={this.props.serviceName}
              onCancel={() => this.setState({ showNew: false })}
            />
          </Card>
        ) : (
          <Button
            className="mt-3"
            onClick={() => this.setState({ showNew: true })}
            data-cy="add-trigger"
          >
            <AddIcon /> <FormattedMessage {...messages.newTrigger} />
          </Button>
        )}

        {this.props.triggers.map((t) => (
          <TriggerSection
            key={t.name}
            {...t}
            deleteTrigger={this.props.deleteTrigger}
          />
        ))}
      </div>
    );
  }
}

const mapStateToProps = (state: AppState, { serviceName }: EditTriggersProps) =>
  createStructuredSelector({
    triggers: ducks.triggerSelectors.byService(serviceName),
  });
type IStateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapDispatchToProps = {
  fetchTriggers: ducks.triggers.actions.fetchAll.request,
  deleteTrigger: (name: string) =>
    createShowConfirmationModal(
      `Are you sure you want to delete '${name}'?`,
      ducks.triggers.actions.delete.request(name),
    ),
};

type IDispatchToProps = typeof mapDispatchToProps;

export default connect(mapStateToProps, mapDispatchToProps)(EditTriggers);



