import { getSystemKey } from 'cb-utils/getPathParams';
import { TimerModel } from 'cb-utils/console-entity-models';
import { IValues, RepeatsOptions, secondsIn, FrequencyType } from './constants';

export function valuesToModel({
  repeatsName,
  frequencyType,
  frequencyNumber,
  ...timer
}: IValues): Partial<TimerModel> {
  let repeats = timer.repeats;
  if (repeatsName === RepeatsOptions.forever) {
    repeats = -1;
  }
  if (repeatsName === RepeatsOptions.once) {
    repeats = 0;
  }
  const frequency = secondsIn[frequencyType] * frequencyNumber || 1; // required even though it doesn't make sense, so random value
  return {
    ...timer,
    system_key: getSystemKey(),
    repeats,
    frequency,
  };
}

export function modelToValues({
  repeats,
  frequency,
  ...timer
}: TimerModel): IValues {
  let repeatsName;
  if (repeats === -1) {
    repeatsName = RepeatsOptions.forever;
  } else if (repeats === 0) {
    repeatsName = RepeatsOptions.once;
  } else {
    repeatsName = RepeatsOptions.other;
  }
  const { frequencyType, frequencyNumber } = convertSecondsToNearest(frequency);
  return {
    ...timer,
    repeatsName,
    repeats,
    frequencyType,
    frequencyNumber,
  };
}

// tried useing dateFns.distanceInWordsStrict but it was not accurate 7 weeks != 1 month
// better to convert it back to the options the user selected
export function convertSecondsToNearest(frequency: number) {
  let frequencyType: FrequencyType;
  let frequencyNumber;
  // gets the lowest round units
  if (frequency % secondsIn.Weeks === 0) {
    frequencyType = FrequencyType.Weeks;
    frequencyNumber = frequency / secondsIn.Weeks;
  } else if (frequency % secondsIn.Days === 0) {
    frequencyType = FrequencyType.Days;
    frequencyNumber = frequency / secondsIn.Days;
  } else if (frequency % secondsIn.Hours === 0) {
    frequencyType = FrequencyType.Hours;
    frequencyNumber = frequency / secondsIn.Hours;
  } else if (frequency % secondsIn.Minutes === 0) {
    frequencyType = FrequencyType.Minutes;
    frequencyNumber = frequency / secondsIn.Minutes;
  } else {
    frequencyType = FrequencyType.Seconds;
    frequencyNumber = frequency / secondsIn.Seconds;
  }
  return { frequencyType, frequencyNumber };
}



