/*
 *
 * RunAsUser
 *
 */

import React from 'react';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { fetchUsers } from 'cb-utils/services/users';
import { UserModel } from 'cb-utils/console-entity-models';
import { Row } from 'reactstrap';
import CbInputField from 'components/CbInputField';
import { SettingTypes, CBEvent } from 'utils/types';
import createFakeEvent from 'cb-utils/createFakeEvent';
import styled from 'styled-components';

const CloseRow = styled(Row)`
  margin-top: -1.5em;
`;

interface RunAsUserProps {
  onChange: (e: CBEvent) => void;
  value: string;
}

interface IState {
  options: UserModel[];
  isLoading: boolean;
  savedEmail: string;
  showRunAs: boolean;
}

export class RunAsUser extends React.PureComponent<RunAsUserProps, IState> {
  state: IState = {
    options: [],
    isLoading: false,
    savedEmail: '',
    showRunAs: !!this.props.value,
  };

  handleToggle = () => {
    const showRunAs = !this.state.showRunAs;
    if (!showRunAs) {
      this.props.onChange(createFakeEvent(''));
    }
    this.setState({ showRunAs });
  };

  render() {
    return (
      <div>
        <Row>
          <CbInputField
            type={SettingTypes.BOOLEAN_TYPE}
            labelText={<FormattedMessage {...messages.runAsAUser} />}
            value={!!this.state.showRunAs}
            onChange={this.handleToggle}
            name=""
            display_name=""
          />
        </Row>
        {this.state.showRunAs && (
          <CloseRow>
            <CbInputField
              onChange={this.props.onChange}
              value={this.props.value}
              type={SettingTypes.ASYNC_TYPE_AHEAD_TYPE}
              caseSensitive
              labelKey="email"
              idKey="user_id"
              fetchPromise={fetchUsers}
              name=""
              display_name=""
            />
          </CloseRow>
        )}
      </div>
    );
  }
}

export default RunAsUser;



