/*
 *
 * EditTimers
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import NewTimer from 'containers/NewTimer';
import { Button } from 'reactstrap';
import * as ducks from 'containers/Console/ducks';
import { Card } from 'material-ui-next';
import { TimerModel } from 'cb-utils/console-entity-models';
import { TrashIcon, AddIcon, TimerIcon } from 'components/EntityIcons';
import { isFuture, format } from 'date-fns';
import { createShowConfirmationModal } from '../Console/actions/ui';
import { convertSecondsToNearest } from '../NewTimer/utils';
import consoleMessages from 'containers/Console/messages';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

export const SuccinctTimer = ({
  name,
  frequency,
  start_time,
  repeats,
}: TimerModel) => {
  const { frequencyNumber, frequencyType } = convertSecondsToNearest(frequency);
  return (
    <div>
      <TimerIcon />{' '}
      {repeats !== 0 && (
        <FormattedMessage
          {...messages.every}
          values={{ frequencyNumber, frequencyType }}
        />
      )}
      {isFuture(start_time) && (
        <span>
          , <FormattedMessage {...messages.starting} />{' '}
          {format(start_time, 'MMM D')}
        </span>
      )}
      {repeats !== -1 && (
        <span>
          ,{' '}
          {repeats === 0 ? (
            <FormattedMessage {...messages.once} />
          ) : (
            <FormattedMessage {...messages.times} values={{ repeats }} />
          )}
        </span>
      )}
    </div>
  );
};

interface ITimerSectionProps extends TimerModel {
  deleteTimer: (name: string) => any;
}

const TimerSection = ({ deleteTimer, ...timer }: ITimerSectionProps) => {
  return (
    <Card className="my-3 mx-1 p-3">
      <Button
        className="float-right"
        data-cy="delete-timer"
        onClick={() => deleteTimer(timer.name)}
      >
        <TrashIcon /> <FormattedMessage {...messages.deleteTimer} />
      </Button>
      <FormattedMessage {...consoleMessages.summary} />
      <b>
        <SuccinctTimer {...timer} />
      </b>
      <NewTimer savedTimer={timer} />
    </Card>
  );
};

interface EditTimersProps {
  serviceName: string;
}

export class EditTimers extends React.PureComponent<
  EditTimersProps & IStateToProps & IDispatchToProps,
  {
    showNew: boolean;
  }
> {
  state = { showNew: false };
  componentWillMount() {
    this.props.fetchTimers();
  }
  render() {
    return (
      <div>
        {this.state.showNew ? (
          <Card className="my-3 mx-1 p-3">
            <NewTimer
              serviceName={this.props.serviceName}
              onCancel={() => this.setState({ showNew: false })}
            />
          </Card>
        ) : (
          <Button
            className="mt-3"
            data-cy="add-timer"
            onClick={() => this.setState({ showNew: true })}
          >
            <AddIcon /> <FormattedMessage {...messages.newTimer} />
          </Button>
        )}

        {this.props.timers.map((t) => (
          <TimerSection
            key={t.name}
            {...t}
            deleteTimer={this.props.deleteTimer}
          />
        ))}
      </div>
    );
  }
}

const mapStateToProps = (state: AppState, { serviceName }: EditTimersProps) =>
  createStructuredSelector({
    timers: ducks.timerSelectors.byService(serviceName),
  });
type IStateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapDispatchToProps = {
  fetchTimers: ducks.timers.actions.fetchAll.request,
  deleteTimer: (name: string) =>
    createShowConfirmationModal(
      `Are you sure you want to delete '${name}'?`,
      ducks.timers.actions.delete.request(name),
    ),
};
type IDispatchToProps = typeof mapDispatchToProps;

export default connect(mapStateToProps, mapDispatchToProps)(EditTimers);



