import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';
import { LogModel } from 'cb-utils/services/service';

/**
 * Direct selector to the serviceDetailPage state domain
 */
const selectServiceDetailPageDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

export const getServiceResult = () =>
  createSelector(
    selectServiceDetailPageDomain(),
    (substate) => substate.serviceRunResult,
  );

export const selectLogs = () =>
  createSelector(selectServiceDetailPageDomain(), (substate) => {
    return substate.logs;
  });

export const sortLogsByExecutionTimeDescending = (logs: LogModel[]) => {
  return logs.slice().sort((a, b) => {
    const itsaDate = new Date(a.service_execution_time);
    const itsbDate = new Date(b.service_execution_time);
    if (itsaDate < itsbDate) {
      return 1;
    } else if (itsaDate > itsbDate) {
      return -1;
    } else {
      return 0;
    }
  });
};

export const selectLogsDescending = () =>
  createSelector(selectLogs(), (substate) => {
    return sortLogsByExecutionTimeDescending(substate);
  });



