/**
 *
 * FailedRunsAlert
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import { Alert } from 'reactstrap';
import RedText from 'components/RedText';

interface FailedRunsAlertProps {
  count: number;
  serviceName?: string;
  createShowBaseModal: (a: any) => void;
}

const AlertWrapper = styled(Alert)`
  border-radius: 0;
`;

const FailedRunsAlert: SFC<FailedRunsAlertProps> = (props) => {
  return (
    <div>
      {props.count ? (
        <AlertWrapper color="warning">
          <div
            onClick={() =>
              props.createShowBaseModal({
                component: 'FailedRunsModal',
                props: { serviceName: props.serviceName },
              })
            }
          >
            <RedText>
              <b>{props.count}</b>
            </RedText>{' '}
            <FormattedMessage
              {...messages.failedRuns}
              values={{ num: props.count }}
            />{' '}
            <a href="#">
              <FormattedMessage {...messages.view} />
            </a>
          </div>
        </AlertWrapper>
      ) : (
        ''
      )}
    </div>
  );
};

export default FailedRunsAlert;



