/**
 *
 * CodeLogs
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';

import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';
import CbButton from 'components/CbButton';
import { Button } from 'reactstrap';
import { LogModel, ServiceResult } from 'cb-utils/services/service';
import ToggleWidget from 'plugins/widgets/FormWidget/widgets/ToggleWidget';
import * as ui from 'containers/Console/actions/ui';
import { ServiceModel } from 'cb-utils/console-entity-models';
import { createGetServiceLogs } from 'containers/ServiceDetailPage/actions';
import RefreshIcon from 'react-icons/lib/fa/refresh';
import { LogItem, CodeBlock } from 'components/LogModal';

export const SIDE_BAR_WIDTH = 250;

const Header = styled.h4`
  font-size: 1.2rem;
`;

const LogWrapper = styled.div`
  height: 100%;
  overflow-y: scroll;
`;

const TestParamsBlock = styled.div`
  min-height: 3em;
  overflow-y: hidden;
`;

const SideWrapper = styled.div`
  border-left: solid #eee;
  min-width: ${SIDE_BAR_WIDTH}px;
  width: ${SIDE_BAR_WIDTH}px;
  height: 100%;
  padding-left: 1em;
  display: flex;
  flex-direction: column;
`;

interface IResponseProps extends ServiceResult {
  createShowBaseModal: typeof ui.createShowBaseModal;
}

interface ILogProps extends LogModel {
  createShowBaseModal: typeof ui.createShowBaseModal;
  allLogs: LogModel[];
  serviceName: string;
}

const Log = ({
  service_execution_time,
  log,
  allLogs,
  serviceName,
  createShowBaseModal,
}: ILogProps) => {
  return (
    <LogItem
      time={service_execution_time}
      codeString={log}
      onShow={() =>
        createShowBaseModal({
          component: 'LogModal',
          size: 'lg',
          props: {
            allLogs,
            serviceName,
            selectedLog: {
              log,
              service_execution_time,
            },
          },
        })
      }
    />
  );
};

const Response = ({
  service_execution_time,
  results,
  logs,
  paramObj,
  success,
  createShowBaseModal,
}: IResponseProps) => {
  if (!results) {
    return (
      <div className="mb-2 text-muted">
        <FormattedMessage {...messages.emptyResults} />
      </div>
    );
  }

  return (
    <LogItem
      time={service_execution_time}
      codeString={results}
      onShow={() =>
        createShowBaseModal({
          component: 'CodeResponseModal',
          size: 'lg',
          props: {
            results,
            service_execution_time,
            success,
            paramObj,
            logs,
          },
        })
      }
    />
  );
};

interface CodeLogsProps extends Partial<ServiceModel> {
  serviceName?: string;
  logs: LogModel[];
  response: ServiceResult;
  handleRun: () => void;
  toggleLogging: () => void;
  createShowBaseModal: typeof ui.createShowBaseModal;
  fetchLogs: typeof createGetServiceLogs;
}

const CodeLogs: SFC<CodeLogsProps & ReactIntl.InjectedIntlProps> = ({
  logs,
  response,
  handleRun,
  paramObj,
  toggleLogging,
  logging_enabled,
  createShowBaseModal,
  serviceName,
  fetchLogs,
  intl,
}) => {
  const openParams = () =>
    createShowBaseModal({
      component: 'CodeParamModal',
      size: 'lg',
      props: {
        paramObj,
        serviceName,
      },
    });
  return (
    <SideWrapper>
      <TestParamsBlock>
        <a
          onClick={openParams}
          href="#"
          data-cy="edit-params"
          className="float-right"
        >
          <FormattedMessage {...messages.editParams} />
        </a>
        <CodeBlock onClick={openParams}>{paramObj}</CodeBlock>
      </TestParamsBlock>
      <br />

      <div className="text-right">
        <CbButton
          color="primary"
          onClick={handleRun}
          data-cy="save-and-test"
          title={intl.formatMessage(messages.saveTestTooltip)}
        >
          <FormattedMessage {...messages.saveTest} />
        </CbButton>
      </div>
      <br />
      <Header className="mt-2">
        <FormattedMessage {...messages.response} />
      </Header>
      <Response {...response} createShowBaseModal={createShowBaseModal} />
      <br />
      <Header>
        <FormattedMessage {...messages.logs} />
        <Button
          size="sm"
          className="ml-1"
          onClick={() => fetchLogs({ serviceName })}
        >
          <RefreshIcon onClick={() => fetchLogs({ serviceName })} />
        </Button>
        <div className="float-right">
          <ToggleWidget onChange={toggleLogging} value={logging_enabled} />
        </div>
      </Header>
      <LogWrapper>
        {logs.map((log, i) => (
          <Log
            key={`${log.service_execution_time}-${i}`}
            {...log}
            allLogs={logs}
            createShowBaseModal={createShowBaseModal}
            serviceName={serviceName}
          />
        ))}
      </LogWrapper>
    </SideWrapper>
  );
};

export default injectIntl(CodeLogs);



