/**
 *
 * NewTimers
 *
 */

import React, { SFC } from 'react';
// import styled from 'styled-components';
import * as ducks from 'containers/Console/ducks';

import messages from './messages';
import { Formik, FormikProps, Field } from 'formik';
import { assetNameValidations } from 'utils/validations';
import FormikForm from 'components/FormikForm';
import CbButton from 'components/CbButton';
import { Button, Row, Col } from 'reactstrap';
import FormikInputWrapper from 'components/FormikInputWrapper';
import { injectIntl, FormattedMessage } from 'react-intl';
import Yup from 'yup';
import { SettingTypes } from 'utils/types';
import makeOptions from 'utils/makeOptions';
import { connect } from 'react-redux';
import { RepeatsOptions, FrequencyType, IValues } from './constants';
import { valuesToModel, modelToValues } from './utils';
import { TimerModel } from 'cb-utils/console-entity-models';

interface NewTimersProps {
  serviceName?: string;
  savedTimer?: TimerModel;
  onCancel?: () => void;
}

const NewTimers: SFC<
  NewTimersProps & IDispatchToProps & ReactIntl.InjectedIntlProps
> = (props) => {
  const { formatMessage } = props.intl;

  const requiredWhenOther = Yup.mixed().when(
    'repeatsName',
    (repeatsName: RepeatsOptions, schema: Yup.MixedSchema) => {
      return repeatsName === RepeatsOptions.other
        ? schema.required(formatMessage(messages.required))
        : schema;
    },
  );

  const notRequiredWhenOnce = Yup.mixed().when(
    'repeatsName',
    (repeatsName: RepeatsOptions, schema: Yup.MixedSchema) => {
      return repeatsName !== RepeatsOptions.once
        ? schema.required(formatMessage(messages.required))
        : schema;
    },
  );

  const sharedProps = {
    component: FormikInputWrapper,
  };

  return (
    <div>
      <Formik
        validationSchema={Yup.object().shape({
          name: assetNameValidations(props.intl),
          repeatsName: Yup.string().required(formatMessage(messages.required)),
          repeats: requiredWhenOther,
          start_time: Yup.string()
            .nullable(true)
            .required(formatMessage(messages.required)),
          frequencyNumber: notRequiredWhenOnce,
          frequencyType: notRequiredWhenOnce,
        })}
        initialValues={
          props.savedTimer
            ? modelToValues(props.savedTimer)
            : {
                start_time: new Date().toISOString(),
                frequencyType: FrequencyType.Minutes,
                repeatsName: RepeatsOptions.forever,
                service_name: props.serviceName,
              }
        }
        onSubmit={(values: IValues, actions) => {
          const newTimer = valuesToModel(values);
          if (props.savedTimer) {
            props.updateTimer(newTimer);
          } else {
            props.createTimer(newTimer);
            props.onCancel();
          }
        }}
        render={(fProps: FormikProps<IValues>) => {
          return (
            <FormikForm {...fProps}>
              <Row>
                <Col>
                  <Field
                    {...sharedProps}
                    disabled={!!props.savedTimer}
                    name="name"
                    label={<FormattedMessage {...messages.name} />}
                  />
                </Col>
                <Col>
                  <Field
                    {...sharedProps}
                    name="start_time"
                    label={<FormattedMessage {...messages.startDate} />}
                    isFakeEvent
                    type={SettingTypes.DATE_PICKER}
                  />
                </Col>
              </Row>
              <Field
                {...sharedProps}
                name="repeatsName"
                label={<FormattedMessage {...messages.repeatsName} />}
                isFakeEvent
                options={makeOptions(RepeatsOptions)}
                type={SettingTypes.OPTION_TYPE}
              />
              {fProps.values.repeatsName === RepeatsOptions.other && (
                <Field
                  {...sharedProps}
                  name="repeats"
                  label={<FormattedMessage {...messages.repeats} />}
                  type={SettingTypes.NUMBER_TYPE}
                />
              )}
              {fProps.values.repeatsName !== RepeatsOptions.once && (
                <div className="d-flex">
                  <div className="mt-4 mr-1">
                    <FormattedMessage {...messages.every} />
                  </div>
                  <Field
                    {...sharedProps}
                    style={{ width: '80px' }}
                    name="frequencyNumber"
                    type={SettingTypes.NUMBER_TYPE}
                  />
                  <Field
                    {...sharedProps}
                    name="frequencyType"
                    options={makeOptions(FrequencyType)}
                    type={SettingTypes.OPTION_TYPE}
                  />
                </div>
              )}
              <Field
                {...sharedProps}
                name="description"
                label={<FormattedMessage {...messages.description} />}
              />
              <br />
              {!props.savedTimer && (
                <Button className="mr-2" onClick={props.onCancel}>
                  <FormattedMessage {...messages.cancel} />
                </Button>
              )}
              <CbButton
                color="primary"
                touched={fProps.touched}
                errors={fProps.errors}
              >
                {props.savedTimer ? (
                  <FormattedMessage {...messages.updateTimer} />
                ) : (
                  <FormattedMessage {...messages.createTimer} />
                )}
              </CbButton>
            </FormikForm>
          );
        }}
      />
    </div>
  );
};

const mapDispatchToProps = {
  createTimer: ducks.timers.actions.create.request,
  updateTimer: ducks.timers.actions.update.request,
};

type IDispatchToProps = typeof mapDispatchToProps;

export default connect(null, mapDispatchToProps)(injectIntl(NewTimers));



