/*
 *
 * ServiceDetailPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';
import CodeLogs, { SIDE_BAR_WIDTH } from 'components/CodeLogs';
import * as ducks from 'containers/Console/ducks';
import ConsolePageFooter from 'components/ConsolePageFooter';
import FailedRunsAlert from 'components/FailedRunsAlert';
import CbCodeEditor, { CbEditorLanguages } from 'components/CbCodeEditor';
import CbButton from 'components/CbButton';
import CodeSettingsForm from 'components/CodeSettingsForm';
import { RouteComponentProps } from 'react-router';
import { Button } from 'reactstrap';
import PageHeader from 'components/ConsolePageHeader';
import ConsolePage from 'components/ConsolePage';
import {
  createShowBaseModal,
  createShowConfirmationModal,
} from 'containers/Console/actions/ui';
import Tabs from 'components/Tabs';
import EditTriggers from 'containers/EditTriggers';
import EditTimers from 'containers/EditTimers';
import { createSaveRoleRequest } from 'containers/Console/actions/roles';
import { updateModifiableService } from 'containers/Console/actions/modifiableEnitites';
import {
  selectModifiableItem,
  selectModifiableItemTouched,
} from 'containers/Console/selectors/modifiableEnitites';
import LibraryTile from 'components/LibraryTile';
import CbCodeModel from 'components/CbCodeEditor/editorModels/CbCodeModel';
// @ts-ignore
import cbDefs from '@types/clearbladejs-server/index.d.ts';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import { isEqual } from 'lodash';
import styled from 'styled-components';
import { triggersTabId, timersTabId } from '../Console/routes';
import { getSystemKey } from 'cb-utils/getPathParams';
import { overallUrls } from 'cb-utils/getUrls';
import { createGetServiceLogs, createSetServiceResult } from './actions';
import { getServiceResult, selectLogsDescending } from './selectors';
import CbPrompt, { confirmCancelHash } from 'components/CbPrompt';
import {
  TimerIcon,
  TriggerIcon,
  CodeIcon,
  SettingsIcon,
  TrashIcon,
  RolesIcon,
} from 'components/EntityIcons';
import { cb } from 'cb-utils/cbLib';
import { AppState } from 'reducers';
import CbDropDown from 'components/CbDropDown';
import { GearDropDown } from 'containers/EdgesPage';
import { createStructuredSelector } from 'reselect';
import { ServiceModel } from 'cb-utils/console-entity-models';

const CODE_WRAP_ID = 'code-wrapper';

const CodeEditorWrapper = styled.div`
  display: flex;
  height: 100%;
`;

interface ServiceDetailPageProps
  extends RouteComponentProps<{
      systemKey: string;
      serviceName: string;
      tab: string;
    }> {}

type MasterProps = ServiceDetailPageProps &
  IDispatchToProps &
  IStateToProps &
  ReactIntl.InjectedIntlProps;

export class ServiceDetailPage extends React.PureComponent<MasterProps> {
  componentWillMount() {
    this.props.fetchLibraries();
    const { serviceName } = this.props.match.params;
    this.props.fetchService({ name: serviceName });
    this.handleGetLogs();
    const query = cb
      .Query()
      .setPage(1, 0)
      .equalTo('service', serviceName);
    this.props.fetchFailedRuns({ query });
  }
  componentWillUnmount() {
    this.props.createSetServiceResult();
  }
  handleGetLogs = () => {
    const { serviceName } = this.props.match.params;
    this.props.createGetServiceLogs({ serviceName });
  };
  handleSave = () => {
    this.props.updateService(this.props.modifiableService);

    // roles are set to modifiable from roles tab
    Object.values(this.props.modifiableRoles).forEach((role) => {
      const oldRole = this.props.roles[role.Name];
      if (!isEqual(role, oldRole)) {
        // only update role if it's changed
        this.props.createSaveRoleRequest(getSystemKey(), role);
      }
    });
  };
  handleRun = () => {
    this.props.updateService(this.props.modifiableService, { runCode: true });
  };
  handleCodeChange = (code: string) => {
    this.props.updateModifiableService({
      name: this.props.service.name,
      code,
    });
  };
  toggleLogging = () => {
    const logging_enabled = !this.props.modifiableService.logging_enabled;

    this.props.updateModifiableService({
      name: this.props.service.name,
      logging_enabled,
    });
  };
  render() {
    const { serviceName } = this.props.match.params;
    const service = this.props.modifiableService;

    if (!this.props.modifiableService) return null;
    // modifiableService may exist after service is delete
    // should add this clean up but for now doublee check
    if (!this.props.service) return null;

    const title = (
      <PageHeader
        header={
          <div>
            {serviceName}{' '}
            <CbDropDown
              triggerEl={
                <GearDropDown data-cy="open-device-settings-dropdown" />
              }
              items={[
                {
                  displayName: (
                    <div>
                      <RolesIcon />{' '}
                      <FormattedMessage {...messages.editPermissions} />
                    </div>
                  ),
                  'data-test': 'edit-services-detail-roles-btn',
                  onClick: () =>
                    this.props.createShowBaseModal({
                      component: 'EditRolesModal',
                      props: {
                        assetClass: 'services',
                        itemName: serviceName,
                        showAll: false,
                        readPermissionText: messages.run,
                      },
                    }),
                },
              ]}
            />
          </div>
        }
        subHeader={
          <FailedRunsAlert
            createShowBaseModal={this.props.createShowBaseModal}
            count={this.props.failedRunsCount}
            serviceName={serviceName}
          />
        }
        actionButton={() => (
          <div>
            {service &&
              service.dependencies.map((name) => (
                <LibraryTile key={name} name={name} />
              ))}
            <Button
              onClick={() =>
                this.props.createShowBaseModal({
                  component: 'AddLibraryModal',
                  props: {
                    assetClass: 'services',
                    name: serviceName,
                  },
                })
              }
            >
              <FormattedMessage {...messages.addLibrary} />
            </Button>
          </div>
        )}
      />
    );

    const { formatMessage } = this.props.intl;
    return (
      <React.Fragment>
        <ConsolePage subheader={title} allowScroll={false}>
          <Helmet
            title={`ClearBlade - ${serviceName || 'Service Detail'}`}
            meta={[
              {
                name: 'description',
                content: 'Description of serviceDetailPage',
              },
            ]}
          />
          <CbPrompt
            when={this.props.hasServiceChanged}
            message={formatMessage(messages.confirmLeave)}
          />
          <Tabs
            useRoutes
            tabs={[
              {
                id: 'CodeEditor',
                'data-cy': 'CodeEditor',
                title: (
                  <span>
                    <CodeIcon /> <FormattedMessage {...messages.codeEditor} />
                  </span>
                ),
                component: (
                  <CodeEditorWrapper id={CODE_WRAP_ID}>
                    <CbCodeEditor
                      wrappingId={CODE_WRAP_ID}
                      sideBarWidth={SIDE_BAR_WIDTH}
                      onSaveShortcut={this.handleSave}
                      onSaveShiftShortcut={this.handleRun}
                      cbModel={new CbCodeModel(serviceName)}
                      tsDefs={[
                        { name: 'clearblade-serverjs', contents: cbDefs },
                      ]}
                      options={{
                        automaticLayout: true,
                      }}
                      language={CbEditorLanguages.JAVASCRIPT}
                      value={service.code}
                      onChange={this.handleCodeChange}
                    />
                    <CodeLogs
                      fetchLogs={this.props.createGetServiceLogs}
                      serviceName={serviceName}
                      handleRun={this.handleRun}
                      logs={this.props.logs}
                      response={this.props.serviceResult}
                      toggleLogging={this.toggleLogging}
                      logging_enabled={service.logging_enabled}
                      paramObj={service.paramObj}
                      createShowBaseModal={this.props.createShowBaseModal}
                    />
                  </CodeEditorWrapper>
                ),
              },
              {
                id: triggersTabId,
                'data-cy': 'Triggers',
                title: (
                  <span>
                    <TriggerIcon /> <FormattedMessage {...messages.triggers} />{' '}
                    ({this.props.triggers.length})
                  </span>
                ),
                component: (
                  <div>
                    <EditTriggers serviceName={serviceName} />
                  </div>
                ),
              },
              {
                id: timersTabId,
                'data-cy': 'Timers',
                title: (
                  <span>
                    <TimerIcon /> <FormattedMessage {...messages.timers} /> ({
                      this.props.timers.length
                    })
                  </span>
                ),
                component: (
                  <div>
                    <EditTimers serviceName={serviceName} />
                  </div>
                ),
              },
              {
                id: 'Settings',
                'data-cy': 'Settings',
                title: (
                  <span>
                    <SettingsIcon /> <FormattedMessage {...messages.settings} />
                  </span>
                ),
                component: (
                  <div className="mt-2">
                    <CodeSettingsForm
                      onChange={(updates) =>
                        this.props.updateModifiableService({
                          name: serviceName,
                          ...updates,
                        })
                      }
                      service={this.props.modifiableService}
                    />
                  </div>
                ),
              },
            ]}
          />
        </ConsolePage>
        <ConsolePageFooter
          leftButtons={() => (
            <Button
              color="link"
              data-cy="delete-service"
              onClick={() => this.props.deleteService(serviceName)}
            >
              <TrashIcon />
              &nbsp; <FormattedMessage {...messages.deleteService} />
            </Button>
          )}
          actionButtons={() => (
            <React.Fragment>
              <CbButton
                className="mr-3"
                onClick={() =>
                  this.props.history.push(
                    overallUrls.services({}, confirmCancelHash),
                  )
                }
              >
                <FormattedMessage {...messages.discardChanges} />
              </CbButton>
              <CbButton
                color="primary"
                className="mr-3"
                onClick={this.handleSave}
              >
                <FormattedMessage {...messages.save} />
              </CbButton>
            </React.Fragment>
          )}
        />
      </React.Fragment>
    );
  }
}

const mapStateToProps = (
  state: AppState,
  { match: { params: { serviceName } } }: ServiceDetailPageProps,
) => {
  return createStructuredSelector({
    serviceResult: getServiceResult(),
    logs: selectLogsDescending(),
    hasServiceChanged: selectModifiableItemTouched({
      assetClass: 'services',
      id: serviceName,
    }),
    service: ducks.services.selectors.item(serviceName),
    libraries: ducks.libraries.selectors.entities,
    roles: ducks.roles.selectors.entities,
    modifiableRoles: ducks.roles.selectors.selectModifiableRoles(),
    timers: ducks.timers.selectors.byService(serviceName),
    triggers: ducks.triggers.selectors.byService(serviceName),
    failedRunsCount: ducks.failedRuns.selectors.filteredCount(),
    modifiableService: selectModifiableItem<ServiceModel>(
      'services',
      serviceName,
    ),
  });
};

type IStateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapDispatchToProps = {
  fetchLibraries: ducks.libraries.actions.fetchAll.request,
  fetchService: ducks.services.actions.fetchOne.request,
  updateService: ducks.services.actions.update.request,
  fetchFailedRuns: ducks.failedRuns.actions.fetchCount.request,
  deleteService: (name: string) =>
    createShowConfirmationModal(
      `Are you sure you want to delete '${name}'?`,
      ducks.services.actions.delete.request(name),
    ),
  createGetServiceLogs,
  createShowBaseModal,
  createSaveRoleRequest,
  updateModifiableService,
  createSetServiceResult,
};

type IDispatchToProps = typeof mapDispatchToProps;

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect, injectIntl)(
  ServiceDetailPage,
);



