/*
 * RunAsUser Messages
 *
 * This contains all the text for the RunAsUser component.
 */
import { defineMessages } from 'react-intl';

export default defineMessages({
  helpText: {
    id: 'app.containers.RunAsUser.helpText',
    defaultMessage: `A code service will fail if the user who triggers it does not have all the permissions needed. 
      'Run as' allows you to override this and specify a user with all the necessary roles.`,
  },
  runAs: {
    id: 'app.containers.RunAsUser.runAs',
    defaultMessage: 'Run as',
  },
  runAsAUser: {
    id: 'app.containers.RunAsUser.runAsAUser',
    defaultMessage: 'Run as a user',
  },
});



