/*
 *
 * ServiceDetailPage reducer
 *
 */

// import assign from 'immutable-assign';
import { ActionTypes, getServiceLogs } from './actions';
import { ActionConstants } from './actionConstants';
import { ServiceResult, LogModel } from 'cb-utils/services/service';
import assign from 'immutable-assign';

export interface State {
  serviceRunResult: ServiceResult;
  logs: LogModel[];
}

export const initialState: State = {
  serviceRunResult: null,
  logs: [],
};

function serviceDetailPageReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.SET_SERVICE_RESULT:
      return assign(state, (s) => s.serviceRunResult, () => action.payload);
    case getServiceLogs.actions.successType:
      return assign(state, (s) => s.logs, () => action.payload);
    default:
      return state;
  }
}

export default serviceDetailPageReducer;



